<template>
  <!--
    
    
    - 
    - 
    - TODO
  -->
  <div class="app">
    <van-sku :value="show" :sku="sku" :goods="goods" @buy-clicked="onBuyClicked" @add-cart="onAddCartClicked"
      @close="makeHide" @stepper-change="num" reset-selected-sku-on-hide reset-stepper-on-hide @changeCart="changeCart"/>
  </div>
</template>
<script>
/**
 * 
 * @displayName shoplistchose
 * @data {Object} sku - 
 * @data {Object} goods - 
 * @data {Array} listArray - 
 * @data {Number} purchaseNum - 
 * @data {Array} other - 
 * @methods {Function} onBuyClicked - 
 * @methods {Function} onAddCartClicked - 
 * @methods {Function} hiden - 
 * @methods {Function} num - 
 * @methods {Function} setList - 
 * @methods {Function} setSku - 
 * @computed {Boolean} show - 
 * @computed {Object} cart - 
 * @created {Function} created - eventBus
 * @created {Function} setList - 
 * @created {Function} setSku - 
 * @beforeDestroy {Function} beforeDestroy - eventBus
 */
import Bus from '@/utils/eventBus'
import { mapState, mapMutations, mapActions } from 'vuex'
export default {
  data () {
    return {
      // 
      sku: {
        // 
        tree: [
          {
            k: '',
            k_s: 's1',
            v: [],
            largeImageMode: false
          },
          {
            k_s: 's2',
            k: '',
            v: []
          }
        ],
        // 
        price: null,
        // 
        stock_num: null,
        // 
        messages: [
          {
            datetime: '0', //  time '1' 
            multiple: '0', //  text '1' 
            name: '', // 
            type: 'text', // : id_no(), text, tel, date, time, email
            required: '1', //  '1' 
            placeholder: '', // 
            extraDesc: '' // 
          }
        ],
        /**
         *
         *@argument {String} phoneNumber - 
         *@argument {String} productId - id
         *@argument {String} color - 
         *@argument {String} size - 
         *@argument {Number} price - 
         *@argument {Number} quantity - 
         *@argument {String} name - 
         *@argument {String} img - 
         *@argument {Object} other - 
         */
        list: []
      },
      goods: {
        picture: ''
      },
      listArray: [],
      purchaseNum: 1,
      other: [],
      needPayMoney: 0,
      currentMoney: 0,
      lastMoney: 0
    }
  },
  methods: {
    /**
     * 
     * TODO
     *
     * 
     * @param data {Object} 
     * @param data.selectedSkuComb {Object} 
     * @param data.selectedSkuComb.id {String} id
     * @param data.selectedSkuComb.s1 {Number} 
     * @param data.selectedSkuComb.s2 {Number} 
     * @param data.selectedSkuComb.price {Number} 
     * @param data.selectedSkuComb.stock_num {Number} 
     * @param data.selectedSkuComb.name {String} 
     * @param data.selectedSkuComb.imgUrl {String} 
     * @param data.selectedSkuComb.other {Object} 
     */
    onAddCartClicked (data) {
      const list = data.selectedSkuComb
      const color = list.s1
      const size = list.s2
      const obj = {
        phoneNumber: JSON.parse(localStorage.getItem('msg')).phoneNumber,
        productId: list.id,
        color: JSON.parse(this.listArray.color)[color],
        size: JSON.parse(this.listArray.sizes)[size].size,
        price: list.price / 100,
        name: list.name,
        img: list.imgUrl,
        quantity: this.purchaseNum,
        other: list.other
      }
      this.addCart(obj)
      this.getCart(JSON.parse(localStorage.getItem('msg')).phoneNumber)
      this.getCart(JSON.parse(localStorage.getItem('msg')).phoneNumber).then(res => {
        console.log(res)
        res.data.data.forEach(item => {
          if (item.productId.startsWith('undefined')) {
            item.productId.replace(/'undefined'/g, +new Date())
            console.log(item.productId)
          }
        })
        localStorage.setItem('cart', JSON.stringify(res.data.data))
      })
      this.makeHide()
      this.$router.push('/cart')
    },
    hiden () {
      this.makeHide()
    },
    /**
     * 
     * @description 
     * @param data {Number} 
     */
    num (data) {
      console.log(data)
      this.purchaseNum = data
    },
    ...mapMutations('cart', ['makeShow', 'makeHide']),
    ...mapActions('cart', ['addCart', 'getCart']),
    setList (val) {
      this.sku.list = []
      const colors = JSON.parse(val.color)
      const sizes = JSON.parse(val.sizes)
      const other = JSON.parse(val.other)
      colors.forEach((item, index) => {
        sizes.forEach((item, index2) => {
          const obj = {
            id: val.id + `${index}${index2}`,
            s1: index,
            s2: index2,
            price: val.price * 100 + index2 * 1000,
            stock_num: parseInt(val.count / (colors.length * sizes.length)),
            name: val.name,
            imgUrl: val.img,
            other: other
          }
          this.sku.list.push(obj)
        })
      })
    },
    setSku (val) {
      this.sku.tree[0].v = []
      this.sku.tree[1].v = []
      if (!val) {
        return new Error('val is not defined')
      }
      this.sku.id = val.id
      this.sku.other = val.other
      this.goods.picture = val.img
      this.sku.price = val.price
      this.sku.stock_num = Number(val.count)
      JSON.parse(val.color).forEach((item, index) => {
        const obj = {
          id: index,
          name: item,
          imgUrl: val.img
        }
        this.sku.tree[0].v.push(obj)
      })
      JSON.parse(val.sizes).forEach((item, index) => {
        const obj = {
          id: index,
          name: item.size,
          price: item.size === '' || item.size === 'S' ? 0 : 20
        }
        this.sku.tree[1].v.push(obj)
      })
    },
    onBuyClicked () {
      this.needPayMoney = this.sku.price * this.purchaseNum * 100
      this.currentMoney = JSON.parse(localStorage.getItem('msg')).totalAssets
      this.lastMoney = this.currentMoney - this.needPayMoney
      if (this.currentMoney < this.needPayMoney) {
        this.$toast.fail('')
      } else {
        localStorage.setItem('msg', JSON.stringify({
          totalAssets: this.lastMoney,
          phoneNumber: JSON.parse(localStorage.getItem('msg')).phoneNumber,
          userName: JSON.parse(localStorage.getItem('msg')).userName
        }))
        this.$toast.success('')
        this.makeHide()
      }
    },
    changeCart (data) {
      this.checked = data.filter(item => item.productId)
      localStorage.setItem('selectCart', JSON.stringify(this.checked))
    }
  },
  computed: {
    ...mapState('cart', ['show', 'cart'])
  },
  created () {
    this.makeHide()
    if (Bus) {
      Bus.$on('set', (val) => {
        this.listArray = val
        this.setSku(val)
        this.setList(val)
      })
    } else {
      console.log('Bus is not defined')
    }
  },
  beforeDestroy () {
    if (Bus) {
      Bus.$off('set')
    }
  }
}
</script>

<style></style>
